<template>
    <div class="mainfood">
        <div class="img_food">
            <img :src="foods.image" @click="goodcoles()">
        </div>
        <div class="food_box">
            <div class="info_food">
                <h3>{{foods.name}}</h3>
                <p>
                    <span>月售{{foods.sellCount}}份</span>
                    <span>好评率{{foods.rating}}%</span>
                </p>
                <div class="food_price">
                    <p class="now_price"><span>¥</span>{{foods.price}}</p>
                    <p class="old_price" v-if="foods.oldPrice"><span>¥</span>{{foods.oldPrice}}</p>
                </div>
            </div>
            <div class="shopping" @click="carshow()">
                <div></div>
                <div class="shopcar" v-show="foods.count == 0">
					<p >加入购物车</p>	
				</div>
				<div v-show="foods.count > 0">
					<car-control :food="foods"></car-control>
				</div>
            </div>
        </div>
        
    </div>
</template>
<script>
	import {mapGetters} from 'vuex';
	import CarControl from '../car/carcontrlo.vue';
	export default {
		computed:{
			...mapGetters([
				'foods',
				'carcontrol'
			]),
		},
		components:{
			'car-control':CarControl
		},
		methods:{
			goodcoles(){
				this.$store.dispatch('goodcoles');
			},
			carshow(){
				console.log("====="+this.foods);
				this.$store.dispatch('carshow');
			}
		}
	}
</script>
<style lang="less" scoped>
.mainfood{
    width: 100%;
    height: 9.65rem;
    // border: 0.01rem solid red;
    background: white;
    border-bottom: 0.02rem solid rgba(7,17,27,0.1);
    box-sizing: border-box;
}

// 商品图片
.img_food{
    width: 100%;
    height: 7.5rem;
    // border: 0.01rem solid blue;
    img{
        width: 100%;
        height: 100%;
    }
}

.food_box{
    // width: 100%;
    padding: 0.36rem;
    display: flex;
    justify-content: flex-start;
    box-sizing: border-box;
}
        
// 商品信息
.info_food{
	width: 100%;
	h3{
		width: 100%;
		height: 0.28rem;
		font-size: 0.28rem;
		color: rgb(7,17,27);
		line-height: 0.28rem;
		overflow: hidden;
		white-space: nowrap;
		text-overflow:ellipsis;
	}
	p{
		width: 100%;
		height: 0.2rem;
		font-size: 0.2rem;
		color: rgb(147,153,159);
		line-height: 0.2rem;
		margin-top: 0.16rem;
        overflow: hidden;
	}
}

// 商品价格
.food_price{
	width: 100%;
	display: flex;
    justify-content: flex-start;
    margin-top: 0.36rem;
	p{
        width: 0.52rem;
        margin-top: 0;
	}
	.now_price{
		height: 0.48rem;
		font-size: 0.28rem;
		color: #f01414;
		font-weight: 700;
		line-height: 0.48rem;
		margin-right: 0.16rem;
		span{
			font-size: 0.2rem;
			font-weight: normal;
		}
	}
	.old_price{
		height: 0.48rem;
		font-size: 0.2rem;
		color: rgb(147,153,159);
		font-weight: 700;
		line-height: 0.48rem;
		text-decoration: line-through;
		span{
			font-weight: normal;
		}
	}
}

// 加减组件
.shopping{
	width: 1.48rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	.shopcar{
		width: 1.35rem;
		height: 0.48rem;
		// border: 0.01rem solid green;
		background:#00a0dc;
		border-radius: 0.24rem;
		text-align: center;
		p{
			color: white;
			font-size: 0.2rem;
			line-height: 0.48rem;
		}
	}
}
</style>



