<template>
    <div>
        <div class="cont-top">
          <div class="all" @click='select(2)' :class="{'active' : selectType === 2 }">{{types[2]}}</div>
          <div class="good" @click='select(0)' :class="{'active' : selectType === 0 }">{{types[0]}}</div>
          <div class="sad" @click='select(1)' :class="{'active' : selectType === 1 }">{{types[1]}}</div>
        </div>
        <div class="cont-mid" @click='toggle'>
          <i class="icon iconfont icon-103 gou" :class="{'only' : onlyContent}"></i>
          <p :class="{'only' : onlyContent}">
            只看有内容的评价
          </p>
        </div>
    </div>  
</template>

<script>
import { mapGetters } from 'vuex'

	export default{
		computed:{
			...mapGetters([
				'types',
                'selectType',
                'onlyContent'
			])
		},
		methods:{
			select( type ){
				this.$store.dispatch('select',{type});
			},
			toggle(){
				this.$store.dispatch('toggle');
			}
		},
		mounted(){
			this.$store.dispatch('changTypes',['推荐','吐槽','全部']);
		}

	}
</script>
<style lang="less" scoped>
.cont-mid,.cont-top{
    box-sizing:border-box;
    display:flex;
}
.cont-top{
    border-bottom:1px solid #e5e6e2;
    padding:0.36rem 0;
    height:1.37rem;
    font-size: 0.24rem;
    .all,.good,.sad{
        width:1.18rem;
        height:100%;
        text-align:center;
        line-height:0.6rem;
        border-radius:0.08rem;
        margin-right:0.16rem;
        &.active{
            color:white;
        }
    }
    .all{
        background:#00a0dc;
    }
    .good{
        background:#ccecf8;
        
    }
    .sad{
        background:#e9ebec;
    }
}
.cont-mid{
    height:0.98rem;
    padding:0.32rem 0;
    font-size: 0.24rem;
    .gou{
        font-size:0.4rem;
        color:#b7bbbf;
        &.only{
            color: #00a0dc;
        }
    }
    p{
        line-height:0.45rem;
        color:gray;
        margin-left:0.16rem;
        &.only{
            color: #00a0dc;
        }
    }
}
</style>



