<template>
  <div class="appheader">
    <!-- 背景图 -->
    <div class="bgimg">
      <img :src='seller.avatar'>
    </div>

    <!-- 商家简介 -->
    <div class="shopInfo">
      <!-- 商家图片 -->
      <img :src='seller.avatar' alt="">
      <!-- 商家店铺信息 -->
      <div class="info">
        <!-- 商家店铺名称 -->
        <div class="shopname">
          <img src="../../static/images/brand.png" alt="">
          <p>{{seller.name}}</p>
        </div>
        <!-- 商家配送方式及到达时间 -->
        <p class="carryout">{{seller.description}}/{{seller.deliveryTime}}分钟到达</p>

        <!-- 优惠信息 -->
        <div class="discount">
            <img src="../../static/images/decrease.png">
            <p v-if="seller.supports">{{ seller.supports[0].description }}</p>
        </div>
        
      </div>
      <!-- 优惠个数 -->
          <div class="disnum" @click="show">
            <p v-if="seller.supports" >{{seller.supports.length}}个 ></p>
          </div>
    </div>

    <!-- 商家公告 -->
    <div class="natice">
      <div class="nat_img">
        <img src="../../static/images/bulletin.png">
      </div> 
      <p class="natext">{{seller.bulletin}}</p>
      <p class="details">＞</p>
        
        <!-- <p>＞</p> -->
    </div>
    <v-info></v-info>
  </div>
</template>

<script>
  import { mapGetters ,mapState } from 'vuex';
  import shopInfo from './shopInfo/index.vue';

  export default {
    components:{
        "v-info":shopInfo
    },
    computed:{
      ...mapGetters([
        'seller',
        // 'flag'
      ])
    },
    methods:{
      show(){
        console.log(1111);
        this.$store.dispatch('show');
      }
    }
  }
</script>

<style lang="less" scoped>
// 背景图
  .appheader{
    overflow: hidden;
    width: 100%;
    height: 2.68rem;
    background: rgba(7,17,27,0.5);
    position: relative;
    .bgimg{
      position: absolute;
      width: 100%;
      height: 100%;
      filter: blur(0.1rem);
      z-index: -5;
      img{
        width: 100%;
        height: 100%;
        
      }
    }
  }

//------------------

// 商家简介
  .shopInfo{
    width: 100%;
    height: 2.12rem;
    padding: 0.48rem 0.24rem 0.36rem 0.48rem; 
    box-sizing: border-box;
    overflow: hidden;
    // 商家图片
    img{
      width: 1.28rem;
      height: 1.28rem;
      border-radius: 0.04rem;
      float: left;
      margin-right: 0.32rem;
    }
  }

  // 商家店铺信息
  .info{
    padding:0.04rem 0;
    float: left;
    width: 4rem;
    // 商家店铺名称
    .shopname{
      font-size: 0.32rem;
      color: rgb(255,255,255);
      font-weight: bold;
      margin-bottom: 0.16rem;
      // box-sizing: border-box;
      img{
        width:0.6rem;
        height: 0.35rem; 
        margin-right: 0.12rem;
      }
    }
    // 商家配送方式及到达时间
    .carryout{
      font-size: 0.24rem;
      color: rgb(255,255,255);
      font-weight: 200;
      line-height: 0.24rem;
      margin-bottom: 0.2rem;
    }
    // 优惠信息
    .discount{
      font-size: 0.2rem;
      color: rgb(255,255,255);
      font-weight: 200;
      line-height: 0.24rem;
      img{
        width: 0.25rem;
        height: 0.25rem;
        margin-right: 0.08rem;
      }
    }
  }

// 优惠个数
  .disnum{
    width: 1rem;
    height: 0.4rem;
    border-radius: 0.4rem;
    background: rgba(0,0,0,0.2);
    padding: 0.14rem 0;
    text-align: center;
    float: right;
    margin-top: 0.5rem;
    p{
      font-size: 0.2rem;
      color: rgb(255,255,255);
      font-weight: 200;
      line-height: 0.4rem
    }
  }
//------------------

// 商家公告
  .natice{
    width: 100%;
    height: 0.56rem;
    background: rgba(7,17,27,0.2);
    padding-left: 0.24rem; 
    padding-right: 0.24rem;
    display: flex;
    justify-content: space-between;
    .nat_img{
      background: white;
      width: 0.45rem;
      height: 0.25rem;
      margin: auto 0;
      // float: left;
      // display: flex;
      // justify-content: space-around;
      img{
        width: 0.45rem;
        height: 0.25rem;
        float: left;
      }
    }
    p{
      float: left;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 0.2rem;
      color: rgb(255,255,255);
      font-weight: 200;
      line-height: 0.56rem;
      text-align: left;
      &.natext{
        width: 6rem;
      }
      &.details{
        width: 0.4rem;
        margin-right: 0.24rem;
      }

    }
    
    
  }
  
</style>



