<template>
  <div class="goodcon" v-show="good_flag" id="foodInfo">
      <div>
        <!-- {{foods}} -->
        <v-title></v-title>
        <div class="blank"></div>
        <v-foods-info></v-foods-info>
        <div class="blank"></div>
        <v-foot-rate></v-foot-rate>
      </div>
  </div>
</template>
<script>
    import { mapGetters } from 'vuex';
    import BScroll from 'better-scroll';
    import Title from './title.vue';
    import foodsInfo from './foodinfo.vue';
    import foodRate from './foodrating.vue'

	export default{
		computed: {
            ...mapGetters([
		    // 映射 this.count 为 store.state.count
                'good_flag',
                'foods'

            ])
        },
        components:{
            'v-title':Title,
            'v-foods-info':foodsInfo,
            'v-foot-rate':foodRate
        },
        watch:{
			seller(){
				// this.$nextTick( () => {
				// 	if( !this.sellerSCroll ){
				// 		this.sellerSCroll = new BScroll( document.getElementById('foodInfo') ,{
				// 		});
				// 	}else{
				// 		this.sellerSCroll.refresh();
				// 	}
				// })
			}
		},
        mounted(){
			setTimeout( () => {
				this.$nextTick( () => {
					if( !this.sellerSCroll ){
						this.sellerSCroll = new BScroll( document.getElementById('foodInfo') ,{
							click : true
						});
					}else{
						this.sellerSCroll.refresh();
					}
				})
			},300)
        }
        // methods:{
        //     goodcoles(){
        //         this.$store.dispatch('goodcoles');
        //     }
        // }

	}
</script>
<style lang="less" scoped>
.goodcon{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0; 
    background: #f3f5f7;
    // height: 10rem;
}
.blank{
    height: 0.32rem;
    background: #f3f5f7;
    border-top: #e6e7e8 solid 0.01rem;
    border-bottom: #e6e7e8 solid 0.01rem;
}
</style>



