<template>
    <div class="main">
        <!-- <div class="carlist"> -->
            <v-car-list></v-car-list>
        <!-- </div> -->
    <!-- 空div，为了调位置 -->
        <div class="blank"></div>
        <div class="bg">
            <!-- 总价 -->
            <div class="Allprice">
                <p class="price">¥&nbsp;{{totalPrice}}</p>
                <p >另需配送费&nbsp;¥&nbsp;{{seller.deliveryPrice}}元</p>
            </div>
            <!-- 起送价 -->
            <div class="sendprice">
                <p >{{payDesc}}</p>
            </div>
        </div>
        <!-- 购物车图片 -->
        <div class="car" @click="carlist(selectFoods)">
            <div class="car2" >
                <i class="icon iconfont icon-gouwuche1"></i>
            </div>
            <div class="count" v-show="totalCount>0">{{totalCount}}</div>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import CarList from '../car/carlist.vue';

    export default {
        components:{
            'v-car-list':CarList
        },
        methods:{
            carlist(list){
                console.log(list);
                this.$store.dispatch('carlist',list);
            }
        },
        computed:{
            ...mapGetters([
            'seller',
            'goods',
            'carlistshow'
            ]),
			selectFoods(){
				let foods = [];
				// 遍历大的分类
				this.goods.forEach( good => {
					// 遍历分类里面的每一个商品
					good.foods.forEach( food => {
						// 如果商品的count大于0  就放进定义的数组中去
						if( food.count > 0 ){
							foods.push(food);
                        }
                        console.log('-------'+food.count);
                    })
                    
				});
				// dom是数据的自然映射，数据改变则dom随之更新
				return foods;
			},
			totalPrice(){
				let total = 0;
				// 对上面的计算属性进行遍历（就是对数组遍历，里面的每一个对象都有count）
				this.selectFoods.forEach( food => {
					total += food.count * food.price;
				});
				return total;
			},
			totalCount(){
				let count = 0 ;
				this.selectFoods.forEach( food => {
					count += food.count;
				});
				return count;
			},
			payDesc(){
				if( this.totalPrice === 0){
					return `￥${this.seller.minPrice}元起送`;
				}else if( this.totalPrice < this.seller.minPrice ){
					return `还差￥${this.seller.minPrice - this.totalPrice}元起送`
				}else{
					return "去结算";
				}
			}
		}
    }
</script>

<style lang="less" scoped>
.main{
    width: 100%;
    height: 1.16rem;
    position: fixed;
    bottom: 0;
    z-index: 1000;
}
//空div 主要是将有内容的div顶下去
.blank{
    height: 0.2rem;
    width: 100%;
}
//有内容的div
.bg{
    width: 100%;
    height: 0.97rem;
    background: #141d27;
    display: flex;
    justify-content: space-between;
    // 总价
    .Allprice{
        height: 0.48rem;
        padding-left:1.36rem;
        margin-top: 0.24rem;
        box-sizing: border-box;
        // border: 1px seashell solid;
        p{
            font-size: 0.24rem;
            color: rgba(255,255,255,0.4);
            line-height: 0.48rem;
            display: inline;
            padding: 0 0.24rem;
        }
        .price{
            font-size: 0.32rem;
            font-weight: 900;
            border-right:0.01rem solid rgba(255,255,255,0.1);
        }
    }
    // 起送价
    .sendprice{
        width: 2.1rem;
        height: 100%;
        background: rgba(255,255,255,0.4);
        p{
            height: 0.48rem;
            width: 100%;
            text-align: center;
            margin-top: 0.24rem;
            box-sizing: border-box;
            font-size: 0.24rem;
            color: rgba(255,255,255,0.4);
            line-height: 0.48rem;
        }
    }
}
// 购物车
.car{
    width: 1.12rem;
    height: 1.12rem;
    background: #141d27;
    border-radius: 1rem;
    position: relative;
    top:-1.16rem;
    left: 0.24rem;
    box-sizing: border-box;
    padding: 0.12rem 0.12rem;
    .car2{
        width: 0.88rem;
        height: 0.88rem;
        border-radius: 0.88rem;
        background: rgba(255,255,255,0.2);
        // margin: 0.12rem auto;
        box-sizing: border-box;
        text-align: center;
        
        i{
        //    height: 0.48rem;
           font-size: 0.48rem;
           color: rgba(255,255,255,0.8);
           line-height: 0.88rem
        }
    }
    .count{
        width: 0.48rem;
        height: 0.32rem;
        border-radius:0.2rem;
        background: rgb(240,20,20);
        line-height: 0.32rem;
        font-size: 0.18rem;
        color: rgb(255,255,255);
        font-weight: 700;
        text-align: center;
        position: relative;
        top: -1rem;
        left: 0.5rem;
    }
}
</style>




