import axios from 'axios';
import TYPES from '../types';
const state = {
    goods:[],
    good_flag:false,
    foods:{},
    carcontrol:false,
    foodslist:[],
    carlistshow:false

}

const getters = {
    goods(state){
        return state.goods;
    },
    good_flag(state){
        return state.good_flag;
    },
    foods(state){
        return state.foods;
    },
    carcontrol(state){
        return state.carcontrol;
    },
    foodslist(state){
        return state.foodslist;
    },
    carlistshow(state){
        return state.carlistshow;
    }
}

const actions = {
    getfoots( { commit } ){
        axios.get('../../../data.json').then((resp)=>{
            console.log(resp.data.goods);
            commit(TYPES.GET_GOODS,resp.data.goods);
        })
    },
    goodshow( {commit},foods ){
        commit(TYPES.FLAG_GOODS,foods);
    },
    goodcoles({commit}){
        commit(TYPES.FLAG_GOODST);
    },
    carshow({commit}){
        commit(TYPES.CAR_SHOW);
    },
    carlist({commit},list){
        commit('carlist',list);
    },
     // 关闭选择食品详情;
    closechoosed( { commit } ){
        // console.log(1111);
		commit( 'closechoosed' );
    },
    clearchoosed( { commit } ){
        // console.log(1111);
		commit( 'clearchoosed' );
    }
}

const mutations = {
    [TYPES.GET_GOODS](state,resp){
        state.goods = resp;
    },
    [TYPES.FLAG_GOODS](state,foods){
        // if(state.good_flag == true){
        //     state.good_flag = false;
        //     return;
        // }
        state.good_flag = true;
        state.foods = foods;
        console.log(state.good_flag);
        console.log(state.foods);
        this.state.ratings.ratings = foods.ratings;
    },
    [TYPES.FLAG_GOODST](state){
        state.good_flag = false;
    },
    [TYPES.CAR_SHOW](state){
        if(state.carcontrol == true){
            state.carcontrol = false;
            return;
        }
        state.carcontrol = true;
    },
    carlist(state,list){
        // console.log(list);
        state.foodslist = list;
        state.carlistshow = true;
        // console.log(state.foodslist);
    },
    closechoosed(state){
        // console.log(2);
        state.carlistshow = false;
    },
    clearchoosed(state){
        // console.log(state.choose.length);
        state.foodslist = [];
        state.carlistshow = false;
    }
}
    
export default {
    state,
    getters,
    actions,
    mutations
}


