<template>
    <div class="rate">
        <div class="top">
            <h3>商品评价</h3>
            <!-- <p class="one" @click="goodType(2)">
                <span>{{good_type[2]}}</span>
                <span class="num">57</span>
            </p>
            <p class="two" @click="goodType(0)">
                <span>{{good_type[0]}}</span>
                <span class="num">57</span>
            </p>
            <p class="three" @click="goodType(1)">
                <span>{{good_type[1]}}</span>
                <span class="num">57</span>
            </p> -->
            <v-select></v-select>
        </div>
        <!-- <div class="watch">
            <i class="icon iconfont icon-103 gou"></i>
            <p>只看有内容的评价</p>
        </div> -->
        <div class="rates" v-for="(v,k) in selectRatings" :key="k">
            <div class="ratecon">
                <p class="time">{{v.rateTime | time}}</p>
                <div class="cons">
                    <i class="icon iconfont icon-zan"></i>
                    <p>{{v.text}}</p>
                </div>
            </div>
            <div class="user">
                <p>{{v.username}}</p>
                <img :src="v.avatar" alt="">
            </div>
        </div>
    </div>
</template>
<script>
import {mapGetters} from 'vuex';
import time from '../../time';
import select from '../select/select';
export default {
    computed:{
        ...mapGetters([
            'foods',
            'selectRatings'
        ])
    },
    components:{
        "v-select":select
    },
    methods:{
    }
}
</script>
<style lang="less" scoped>
.rate{
    padding: 0.36rem;
    background: white;
}

.top{
    // width: 100%;
    overflow: hidden;
    padding-bottom: 0.32rem;
    border-bottom: 0.01rem solid rgba(7,17,27,0.2);
	h3{
		// width: 100%;
		height: 0.28rem;
		font-size: 0.28rem;
		color: rgb(7,17,27);
		line-height: 0.28rem;
		overflow: hidden;
		white-space: nowrap;
		text-overflow:ellipsis;
    }
    p{
		width: 1.2rem;
		height: 0.32rem;
		font-size: 0.24rem;
		line-height: 0.32rem;
		margin-top: 0.32rem;
        overflow: hidden;
        padding: 0.16rem;
        border-radius: 0.24rem;
        float: left;
        margin-right: 0.16rem;
        text-align: center;
        .num{
            font-size: 0.16rem;
        }
        &.one{
            background:rgb(0,160,220);
            color: rgb(255,255,255);
        }
        &.two{
            background:rgba(0,160,220,0.2);
            color: rgb(77,85,93);
        }
        &.three{
            background:rgba(77,85,93,0.2);
            color: rgb(77,85,93);
        }
	}
}
  
.watch{
    // width: 100%;
    display: flex;
    justify-content: flex-start;
    color: rgb(147,153,159);
    padding: 0.36rem 0;
    border-bottom: 0.01rem solid rgba(7,17,27,0.2);
    i{
        height: 0.48rem;
        font-size: 0.48rem;
        line-height: 0.48rem;
        margin-right: 0.08rem;
    }
    p{
        height: 0.48rem;
        font-size: 0.24rem;
        line-height: 0.48rem;
        
    }
}

.rates{
    // width: 100%;
    padding: 0.32rem;
    border-bottom: 0.01rem solid rgba(7,17,27,0.2);
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    background: white;
    .ratecon{
        margin-bottom: 0.12rem;
        color: rgb(147,153,159);
        .time{
            height: 0.48rem;
            font-size: 0.2rem;
            line-height: 0.48rem;
        }
        .cons{
            display: flex;
            justify-content: flex-start;
            height: 0.48rem;
            line-height: 0.48rem;
            i{
                // height: 0.48rem;
                font-size: 0.24rem;
                // line-height: 0.48rem;
                margin-right: 0.08rem
            }
            p{
                // height: 0.32rem;
                font-size: 0.24rem;
                color: rgb(7,17,27);
                // line-height: 0.32rem;

            }
        }
    }
    .user{
        overflow: hidden;
        p{
            height: 0.24rem;
            float: left;
            margin-right: 0.12rem;
            font-size: 0.2rem;
            color: rgb(147,153,159);
            line-height: 0.24rem;
        }
        img{
            float: right;
            width: 0.24rem;
            height: 0.24rem;
            border-radius: 0.24rem;
        }
    }
}

</style>



