<template>
  <div>
    <v-header></v-header>
    <div class="routers">
      <router-link to="/good" tag="button" class="fonts">商品</router-link>
      <router-link to="/eval" tag="button" class="fonts">评价</router-link>
      <router-link to="/shop" tag="button" class="fonts">商家</router-link>
    </div>
    <router-view></router-view>
    <!-- <v-flooter></v-flooter> -->
  </div>
</template>

<script>
  import header from './components/header';
  export default {
    name: 'app',
    components:{
      "v-header":header,
    },
    mounted(){
      this.$store.dispatch('getseller');
      this.$store.dispatch('getratings');
      this.$store.dispatch('getfoots');
    }
  }
</script>

<style lang="less" scoped>

// 路由样式
.routers{
  width: 100%;
  height: 0.8rem;
  display: flex;
  justify-content: space-around;
  line-height: 0.8rem;
  border-bottom:0.01rem solid rgba(7,17,27,0.1);
  .fonts{
    font-size: 0.28rem;
    color: rgb(77,28,93);
    border: none;
    background: white;
    outline: none;
    &.router-link-active{
      color: rgb(240,20,20);
    }
  }
}

</style>




