<template>
  <div class="foods_decri">
      <h3>商品介绍</h3>
      <p>{{foods.info}}</p>
  </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    export default {
        computed:{
            ...mapGetters([
                'foods'
            ])
        }
    }
</script>
<style lang="less" scoped>
.foods_decri{
    padding: 0.36rem;
    background: white;
    border-bottom: 0.02rem solid rgba(7,17,27,0.1);
    border-top: 0.02rem solid rgba(7,17,27,0.1);
    h3{
        height: 0.28rem;
        font-size: 0.28rem;
        color: rgb(7,17,27);
        line-height: 0.28rem;
        margin-bottom: 0.12rem;
    }
    p{
        margin: 0 0.16rem;
        font-size: 0.24rem;
        font-weight: 200;
        color: rgb(77,85,93);
        line-height: 0.48rem;
    }
}
</style>



