
<template>
    <!-- 商家 -->
  <div class="main">
      <!-- 店铺名称 收藏 评价  -->
      <div class="shop">
          <!-- 店铺信息 -->
          <div class="shopname">
              <p>{{seller.name}}</p>
              <div class="score">
                <el-rate
                    v-model="seller.score"
                    disabled
                    text-color="#ff9900"
                    >
                </el-rate>
                  <!-- <div class="star">
                    <img src="../../../static/images/star_on.png" alt="1">
                    <img src="../../../static/images/star_on.png" alt="2">
                    <img src="../../../static/images/star_on.png" alt="3">
                    <img src="../../../static/images/star_on.png" alt="4">
                    <img src="../../../static/images/star_off.png" alt="5" >
                  </div> -->
                  <div class="sell">
                        <p>
                            <span>(661)</span>
                            <span>月售690单</span>
                        </p>
                  </div>
              </div>
          </div>
          <!-- 收藏 -->
          <div class="collect">
              <div class="icon iconfont icon-aixin"></div>
              <p>已收藏</p>
          </div>
      </div>

      <!-- 起送价 配送方式 配送时间  -->
      <div class="deliver">
          <div class="minprice">
              <p class="title">起送价</p>
              <p>
                  <span class="num">{{seller.minPrice}}</span>
                  <span class="unit">元</span>
              </p>
          </div>
          <div class="delive">
              <p class="title">{{seller.description}}</p>
              <p>
                  <span class="num">4</span>
                  <span class="unit">元</span>
              </p>
          </div>
          <div class="deliveryTime">
              <p class="title">平均配送时间</p>
              <p>
                  <span class="num">{{seller.deliveryTime}}</span>
                  <span class="unit">分钟</span>
              </p>
          </div>
      </div>
  </div>
</template>

<script>
    import { mapGetters } from 'vuex';

    export default {
        computed:{
            ...mapGetters([
                'seller'
            ])
        }
    }
</script>

<style lang="less" scoped>
// 商家
.main{
    width: 100%;
}

// 上面
.shop{
    width: 100%;
    // height: 1.24rem;
    border-bottom: 0.01rem solid rgba(7,17,27,0.1);
    padding: 0.36rem;
    box-sizing: border-box;
    overflow: hidden;
    // 店铺信息
    .shopname{
        height: 100%;
        float: left;
        p{
            height: 0.28rem;
            font-size: 0.28rem;
            color: rgb(7,17,27);
            line-height: 0.28rem;
        }
    }
    // 收藏
    .collect{
        height: 100%;
        float: right;
        text-align: center;
        p{
            height: 0.2rem;
            font-size: 0.2rem;
            color: rgb(77,85,93);
            line-height: 0.2rem;
        }
        div{
            font-size: 0.48rem;
            color: rgb(240,20,20);
            margin-bottom: 0.08rem;
        }
    }
}

// 评价
.score{
    height: 0.36rem;
    width: 100%;
    margin-top: 0.16rem;
    display: flex;
    justify-content: space-between;
    // .star{
    //     width: 2.2rem;
    //     height: 100%;
    //     overflow:hidden;
    //     // float: left;
    //     margin-left: 0.16rem;
    //     img{
    //         float: left;
    //         height: 100%;
    //         width: 0.36rem;
    //     }
    // }
    .sell{
        // float: right;
        height: 0.36rem;
        p{
            height: 100%;
            font-size: 0.2rem;
            color: rgb(77,85,93);
            line-height: 0.36rem;
            span{
                display: inline-block;
                margin-right:0.24rem;
            } 
        }
    }
}

// 下面配送
.deliver{
    width: 100%;
    padding: 0.36rem 0;
    display: flex;
    justify-content: space-between;
    div{
        height: 100%;
        margin: 0 auto;
        width: 2.5rem;
        text-align: center;
    }
    
}
.delive{
    border-right:rgba(7,17,27,0.1) solid 0.01rem;
    border-left: rgba(7,17,27,0.1) solid 0.01rem;
}
.title{
    font-size: 0.2rem;
    color: rgb(147,153,159);
    line-height: 0.2rem;
    margin-bottom: 0.08rem;
}
.num{
    font-size: 0.48rem;
    font-weight: 200;
    color: rgb(7,17,27);
    line-height: 0.48rem;
}
.unit{
    font-size: 0.2rem;
    font-weight: 200;
    color: rgb(7,17,27);
    line-height: 0.48rem;
}
</style>



