<template>
  <div>
      <!--优惠信息结束-->
    <div class="box1">
        <div class="line1"></div>
        <div class="txt">优惠信息</div>
        <div class="line2"></div>
    </div>
    <!--优惠信息结束--> 
    <div class="clear"></div>  
    
    <!--优惠信息详情开始-->
    <div class="info"> 
        <!--减开始-->
        <div class="row" v-for="(v,k) in seller.supports" :key="k">
            <img src="../../../static/images/decrease.png">
            <span v-if='seller.supports' class="des">{{v.description}}</span>
        </div>
        <!--减结束--> 
    </div> 
    <!--优惠信息详情结束-->   
  </div>
</template>
<script>
    import { mapState,mapGetters } from 'vuex'
	// import animate from '../../../static/css/animate.css'

	export default{
		computed: {
            ...mapGetters([
                // 映射 this.count 为 store.state.count
                'seller',
                'flag',

            ])
        }
	}
</script>
<style lang="less" scoped>
/*优惠信息开始*/
.box1{
    text-align: center;
    font-size:0.3rem;
    font-weight:700;
    margin-top:0.24rem;
    .line1{
        border:1px solid #666;
        width:2.24rem;
        margin-left:0.65rem;
        float:left;
    }
    .txt{
        margin:0 0.24rem;
        float:left;
        line-height: 0.1rem;
    }
    .line2{
        border:1px solid #666;
        width:2.24rem;
        float:left;
    }
}
.clear{
    clear:both;
}
/*优惠详情开始*/
.info{
    padding-left:0.9rem;
    margin-top:0.48rem;
    .row{
        height:0.38rem;
        line-height: 0.38rem;           
        margin-bottom:0.24rem;
        img{
            height:0.38rem;
        }
        .des{
            position: relative;
            bottom:0.1rem;
            margin-left:0.12rem;
        }
    }
/*优惠详情样式结束*/
}
</style>





