import Vue from 'vue'
import Router from 'vue-router'
import header from '../components/header.vue'
import shop from '../components/shop/index.vue'
import good from '../components/goods/index.vue'
import evaluation from '../components/evaluation/index.vue'

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path:"/",
    //   name:"app-header",
    //   component:header
    // },
    {
      path:"/shop",
      // name:"app-header",
      component:shop
    },
    {
      path:"/good",
      // name:"app-header",
      component:good
    },
    {
      path:"/eval",
      // name:"app-header",
      component:evaluation
    }
  ]
})



