<template>
  <div id="main" class="goods">
      <!-- <v-menu></v-menu>
      <v-con></v-con> -->
      <!-- <div class="cc" @click="goodshow"></div> -->
      <!-- 左边 -->
    <div class="left" ref='left'>
        <div>
            <div :class='{"current" : currentIndex == k }' class="menu" v-for="(v,k) in goods" :key="k" @click='selectMenu(k)'>
                <div>
                    <p>
                        {{v.name}}
                    </p>
                </div>
            </div>
        </div>
    </div>
    <!-- 右边 -->
    <div class="right" ref='right'>
        <div>
            <div class="model food-list-hook" v-for="(v,k) in goods" :key="k">
                <p class="heading">{{v.name}}</p>
          <div class="r_foods" >
            <!-- 商品 -->
            <div class="foodInfo" v-for="(item,i) in v.foods" :key="i" >
              <!-- 图片 -->
              <div class="f_img" >
                <img :src="item.icon" @click="goodshow(item)">
              </div>
              <!-- 商品信息 -->
              <div class="info">
                <h3>{{item.name}}</h3>
                <p v-if="item.description">{{item.description}}</p>
                <p>
                  <span>月售{{item.sellCount}}份</span>
                  <span>好评率{{item.rating}}%</span>
                </p>
                <div class="price">
                  <p class="nowprice"><span>¥</span>{{item.price}}</p>
                  <p class="oldprice" v-if="item.oldprice"><span>¥</span>{{item.oldprice}}</p>
                </div>
              </div>
              <!-- 加减组件 -->
              <div class="num">
                <div class="kong"></div>
                <div class="com">
                  <car-control :food="item"></car-control>
                </div>
              </div>
            </div>
          </div>
            </div>
        </div>
        
    </div>
    
    <v-footer></v-footer>
    <v-good-info></v-good-info>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import BScroll from 'better-scroll';
  import footer from '../footer/footer.vue';
  import goodInfo from '../food/index.vue';
  import CarControl from '../car/carcontrlo.vue';
  export default {
    data(){
			return {
				heights:[],
				scrollY : ''
			}
		},
    components:{
      // "v-menu":Menu,
      // "v-con":Content,
      'v-footer':footer,
      'v-good-info':goodInfo,
      'car-control':CarControl
    },
    computed:{
      ...mapGetters([
          'goods',
          'foods'
			]),
			// 计算当前位置在哪一区间内 然后返回当前i
			currentIndex(){
				for( let i = 0 ; i < this.heights.length ; i++){
					let height1	= this.heights[i];
					let height2	= this.heights[i+1];
					if( this.scrollY+10 >= height1 && this.scrollY+10 < height2){
						return i;
					}
				}
				return 0;
			}
    },
    mounted(){
			this.$nextTick( () => {
        setTimeout(() => {
          this.initBScroll();
				  this.calcHeight();
        })
				
			})
    },

		methods:{
      // 右侧点击事件
			goodshow(data){
				// console.log(123);
				this.$store.dispatch('goodshow',data);
				
			},
      initBScroll(){
          // 右侧滚动
            if( !this.rightBScroll ){
              this.rightBScroll = new BScroll( this.$refs.right,{
                click : true,
                probeType : 3
              })
            }else{
              this.rightBScroll.refresh();
            };
            this.rightBScroll.on('scroll', (pos) => {
              // console.log(pos);
              this.scrollY = Math.abs( Math.round( pos.y))
              // console.log(this.scrollY);
            }) 

            // 左侧滚动
            if( !this.leftBScroll ){
              this.leftBScroll = new BScroll( this.$refs.left,{
                click : true
              })
            }else{
              this.leftBScroll.refresh();
            };
        },
      calcHeight(){
        let foodList = document.getElementsByClassName('food-list-hook');
        let height = 0 ;
        this.heights.push( height );
        for(let i = 0 ; i < foodList.length ; i++){
          //获取每一个li（区块）的高度
          height += foodList[i].clientHeight;
          this.heights.push( height );
        }
        // [0, 599, 684, 769, 968, 1110, 1252, 1451, 1765, 2136]
        console.log(this.heights);

      },
        // 左侧点击事件
      selectMenu(k){
        console.log(k)
        let foodList = document.getElementsByClassName('food-list-hook');
        this.rightBScroll.scrollToElement(foodList[k],300);
      }
    },

  }
</script>


<style lang="less" scoped>
.goods{
	height: 10rem;
	display: flex;
  justify-content: space-around;
  background: #f3f5f7;
}
.cc{
  width: 0.5rem;
  height: 0.5rem;
  background: blue;
  position: absolute;
}

// 左边
.left{
    width: 1.6rem;
    border-right: 0.02rem solid #d9dde1;
    height: 9rem;
    overflow: hidden;
}
.menu{
    width: 100%;
    height: 1.08rem;
    background: #f3f5f7;
    text-align: left;
    position: relative;
    &.current{
			background: white;
		}
    div{
        width: 1.12rem;
        margin: 0 0.24rem;
        position: absolute;
        top:50%;
        p{
            font-size: 0.24rem;
            color: rgb(7,17,27);
            font-weight: 200;
        }
    }
}

// 右边
.right{
	// height: 40rem;
	width: 100%;
	// z-index: 100;
	overflow: hidden;
}
.model{
	// width: 100%;
	.heading{
		// width: 100%;
		height:0 .52rem;
		// background: #f3f5f7;
		font-size: 0.24rem;
		color: rgb(147,153,159);
		line-height: 0.52rem;
		margin-left: 0.28rem;
	}
	.r_foods{
		// width: 100%;
		background: white;
		.foodInfo{
			// width: 100%;
			border-bottom:  0.01rem solid rgba(7,17,27,0.1);
			padding: 0.36rem;
			display: flex;
			justify-content: flex-start;
			box-sizing: border-box;
		}
	}
}

// 商品图片
.f_img{
	width: 2rem;
	height: 1.18rem;
	margin-right: 0.2rem;
	// border: 0.01rem solid red;
	img{
		width: 100%;
		height: 100%;
	}
}

// 商品信息
.info{
	width: 100%;
	h3{
		width: 100%;
		height: 0.28rem;
		font-size: 0.28rem;
		color: rgb(7,17,27);
		line-height: 0.28rem;
		margin-top: 0.04rem;
		overflow: hidden;
		white-space: nowrap;
		text-overflow:ellipsis;
	}
	p{
		width: 100%;
		height: 0.2rem;
		font-size: 0.2rem;
		color: rgb(147,153,159);
		line-height: 0.2rem;
		margin-top: 0.16rem;
		overflow: hidden;
	}
}

// 商品价格
.price{
	width: 100%;
	display: flex;
	justify-content: flex-start;
	p{
		width: 0.52rem;
	}
	.nowprice{
		height: 0.48rem;
		font-size: 0.28rem;
		color: #f01414;
		font-weight: 700;
		line-height: 0.48rem;
		margin-right: 0.16rem;
		span{
			font-size: 0.2rem;
			font-weight: normal;
		}
	}
	.oldprice{
		height: 0.48rem;
		font-size: 0.2rem;
		color: rgb(147,153,159);
		font-weight: 700;
		line-height: 0.48rem;
		text-decoration: line-through;
		span{
			font-weight: normal;
		}
	}
}

// 加减组件
.num{
	width: 1.35rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	.com{
		width: 1.35rem;
		height: 0.4rem;
		// border: 0.01rem solid green;
	}
}
</style>





