import axios from 'axios';
import TYPES from '../types';

const state = {
    seller:{},
    flag:false
}

const getters = {
    seller( state ){
        return state.seller;
    },
    flag( state ){
        return state.flag;
    }
}

const actions = {
    getseller( {commit} ){
        axios.get('../../../data.json').then((resp)=>{
            // console.log(resp.data.seller);
            commit(TYPES.GET_SELLER,resp.data.seller);
        })
    },

    show( {commit} ){
        commit(TYPES.FLAG_SELLER);
    }
}

const mutations = {
    [TYPES.GET_SELLER](state,resp){
       state.seller = resp;
    },
    [TYPES.FLAG_SELLER]( state ){
       
        if(state.flag == true){
            state.flag = false;
            return;
        }
        state.flag = true;
        // console.log(state.flag);
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}


