<template>
  <div class="shopInfo">
      <p class="title">商家信息</p>
      <div class="infos" v-for="(v,k) in seller.infos" :key="k">
          <p>{{v}}</p>
      </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';
export default {
    computed:{
        ...mapGetters([
        'seller'
        ])
    },  
}
</script>

<style lang="less" scoped>
.shopInfo{
    width: 100%;
    padding: 0.24rem;
}
.title{
    height: 0.28rem;
    font-size: 0.28rem;
    color: rgb(7,17,27);
    line-height: 0.28rem;
    margin-bottom: 0.24rem;
}
.infos{
    padding:0.32rem 0.24rem;
    border-top: 0.01rem solid rgba(7,17,27,0.1);
    p{
        font-size: 0.24rem;
        font-weight: 200;
        color: rgb(7,17,27);
    }
}
</style>



