<template>
    <div>
<!--商家公告结束-->
        <div class="box1" id="notic">
            <div class="line1"></div>
            <div class="txt">商家公告</div>
            <div class="line2"></div>
        </div>
        <!--商家公告结束-->  
        <div class="clear"></div>  

        <!--商家详情结束-->
        <div class="sellerInfo">
            {{seller.bulletin}}
        </div>
        <!--商家详情结束-->                                                                                                      
    </div>
</template>
<script>
	import { mapState,mapGetters } from 'vuex'
	import count from './count.vue';

	export default{
		computed: mapGetters([
		  // 映射 this.count 为 store.state.count
		  'seller',
          'flag',

		]),
		methods:{
			show(){
				this.$store.dispatch('show');
			}
        },
        components:{
            "v-count":count
        }

	}
</script>
<style lang="less" scoped>
.box1{
    text-align: center;
    font-size:0.3rem;
    font-weight:700;
    margin-top:0.24rem;
    .line1{
        border:1px solid #666;
        width:2.24rem;
        margin-left:0.65rem;
        float:left;
    }
    .txt{
        margin:0 0.24rem;
        float:left;
        line-height: 0.1rem;
    }
    .line2{
        border:1px solid #666;
        width:2.24rem;
        float:left;
    }
}
.clear{
    clear:both;
}


/*商家公告样式开始*/
#notic{
    margin:0.6rem 0;
}
.sellerInfo{
    font-weight: 200;
    margin:0.48rem 0.6rem;
    line-height:0.48rem;
    padding:0 0.24rem;
}
/*商家公告样式结束*/

</style>




