
<template>
	<transition enter-active-class = 'animated fadeIn' leave-active-class = 'animated fadeOut'>
		
		<div class="sellerDetail" v-show='flag'>
            <div class="bg"></div>
			<div class="content" @click='show'>
                <!--店名开始-->
			    <div class="name">{{seller.name}}</div>
                <!--店名结束-->
                <!--星级开始-->
                <div class="rank">
                    <el-rate
                        v-model="seller.score"
                        disabled
                        text-color="#ff9900"
                        >
                    </el-rate>
                </div>
                <!--星级结束--> 
                <v-count></v-count>
                <v-info></v-info>

            <div class="botbox">
                <i class="icon iconfont icon-cha close"></i>
            </div>
		</div>
		</div>
		
	</transition>
</template>

<script>
	import { mapState,mapGetters } from 'vuex';
    import count from './count.vue';
    import info from './info.vue';

	export default{
		computed: mapGetters([
		  // 映射 this.count 为 store.state.count
		  'seller',
          'flag',

		]),
		methods:{
			show(){
				this.$store.dispatch('show');
			}
        },
        components:{
            "v-count":count,
            "v-info":info
        }

	}
</script>

<style lang='less' scoped>
	.sellerDetail{
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
        top: 0rem;
        z-index: 1000;
        /*模糊背景样式开始*/
		.bg{
            width:100%;
            height:100%;
            background: rgba(7,17,27,0.8);
            filter: blur(10);
        }
        /*模糊背景样式结束*/
        
        /*关闭窗口*/    
        .botbox{
            position: relative;
            bottom:-0.64rem;
            padding-left:3.2rem;
            .close{
                color:rgba(255,255,255,0.4);
                font-size:0.62rem;
            }
        }
    }
    .content{
            position: absolute;
            left: 0;
            top: 0;
            padding-top:1.28rem;
            width:100%;
            height:100%;
            font-size:0.24rem;
            color:white;
            /*商家店名样式开始*/
            .name{
                text-align: center;
                font-size:0.32rem;
                font-weight:700;
            }
            /*店名样式结束*/
            /*商家公告样式开始*/
            #notic{
                margin:0.6rem 0;
            }
            .sellerInfo{
                font-weight: 200;
                margin:0.48rem 0.6rem;
                line-height:0.48rem;
                padding:0 0.24rem;
            }
            /*商家公告样式结束*/
        }
    /*评分样式开始*/
    .rank{
        text-align: center;
        padding:0.32rem 0.2rem;
        font-size: 0.366rem;
        // img{
        //     text-align: center;
        //     width:0.4rem;
        //     padding:0.32rem 0.2rem;
        // }
    }
    /*评分样式结束*/
</style>


