<template>
  <div class="cartControl">
      <!-- 减 -->
		<div class="reduce" @click.stop='reduce' v-show="food.count>0">
            <i class="icon iconfont icon-jian"></i>
        </div>
		<div class="count" v-show="food.count>0">{{food.count}}</div>
        <!-- 加 -->
		<div class="add" @click.stop='add'>
            <i class="icon iconfont icon-add"></i>
        </div>
	</div> 
</template>
<script>
import Vue from 'vue'

	export default{

		props:['food'],
		methods:{
			add(){
				if( !this.food.count ){
					Vue.set(this.food , 'count' , 1);
				}else{
					this.food.count ++;
				}
			},
			reduce(){
				if( this.food.count ){
					this.food.count --;
				}
			}
		}
	}
</script>

<style lang="less" scoped>
.cartControl{
    display: flex;
    justify-content: space-around;
    div{
        font-size: 0.2rem;
        color: rgb(147,153,159);
        line-height: 0.4rem;
        i{
            font-size: 0.4rem;
            color: #00a0dc;
        }
    }
}
</style>




