<template>
  <div class="shopimg" >
        <p class="title">商家实景</p>
        <div class="shoppic" ref="shopimg">
            <ul class="pic-list" ref = 'piclist'>
                <li class="pic-item" v-for='(pic,k) in seller.pics' :key="k">
                    <img :src="pic" width="120" height="90">
                </li>
            </ul>
        </div>
  </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import BScroll from 'better-scroll';
    export default {
        computed:{
            ...mapGetters([
            'seller'
            ])
        },
        watch:{
			seller(){
				this._initPicture();
			}
		},
		mounted(){
			this._initPicture();
		},
		methods:{
			_initPicture(){
				if( this.seller.pics){
					let pw = 120;
					let m = 12;
					let w = ( pw + m ) * this.seller.pics.length - m;
					this.$refs.piclist.style.width = w + 'px';

					if( !this.picBScroll ){
						this.picBScroll = new BScroll( this.$refs.shopimg ,{
							scrollX : true ,
							eventPassthrough : 'vertical'
						})
					}else{
						this.picBScroll.refresh();
					}
				}

			}
		
    }
}
</script>

<style lang="less" scoped>
.shopimg{
    width: 100%;
    padding: 0.36rem;
}
.title{
    height: 0.28rem;
    font-size: 0.28rem;
    color: rgb(7,17,27);
    line-height: 0.28rem;
    margin-bottom: 0.24rem;
}
.shoppic{
    height: 1.8rem;
    .pic-list{
        height: 100%;
        list-style: none;
        overflow: hidden;
        white-space: nowrap;
    }
    .pic-item{
        display: inline-block;
        width: 2.4rem;
        height: 100%;
        margin-right: 0.12rem;
        // float: left;
        
        img{
            width: 2.4rem;
            height: 100%;
        }
    }
    
}
</style>



