<template>
  <div class="div" id="main">
    <div>
          <!-- 商家 -->
        <v-name></v-name>
        <div class="blank"></div>
        <v-activations></v-activations>
        <div class="blank"></div>
        <v-shopimg></v-shopimg>
        <div class="blank"></div>
        <v-info></v-info>
        
    </div>
    <v-footer></v-footer>
  </div>
  
</template>

<script>
    import { mapGetters } from 'vuex';
    import name from './name.vue';
    import activations from './activations.vue';
    import shopimg from './shopimg.vue';
    import shopInfo from './shopInfo.vue';
    import footer from "../footer/footer.vue";
    import BScroll from 'better-scroll';

    export default {
        components:{
            "v-name":name,
            "v-activations":activations,
            'v-shopimg':shopimg,
            "v-info":shopInfo,
            "v-footer":footer
        },
        computed:{
            ...mapGetters([
            'seller'
            ])
        },
        watch:{
			seller(){
				this.$nextTick( () => {
					if( !this.sellerSCroll ){
						this.sellerSCroll = new BScroll( document.getElementById('main') ,{
						});
					}else{
						this.sellerSCroll.refresh();
					}
				})
			}
		},
		mounted(){
			setTimeout( () => {
				this.$nextTick( () => {
					if( !this.sellerSCroll ){
						this.sellerSCroll = new BScroll( document.getElementById('main') ,{
						});
					}else{
						this.sellerSCroll.refresh();
					}
				})
			},300)
		}
    }
</script>

<style lang="less" scoped>
.div{
    width: 100%;
    height: 10rem;
    overflow: hidden;
}
.blank{
    height: 0.32rem;
    background: #f3f5f7;
    border-top: #e6e7e8 solid 0.01rem;
    border-bottom: #e6e7e8 solid 0.01rem;
}

</style>





