<template>
	<div class="ratings" id="main">
    <div>
      <div class="top">
        <div class="left">
          <p class="score">{{seller.score}}</p>
          <p class="rat1">综合评分</p>
          <p class="rat2">高于周边商家{{seller.rankRate}}%</p>
        </div>
        <div class="right">
          <p class="service">
            <span>服务态度</span>
						<span class="star">
							<el-rate
									v-model="seller.serviceScore"
									disabled
									text-color="#ff9900"
									>
							</el-rate>
						</span>
          </p>
          <p class="goodsRat one">
            <span>菜品评价</span>
						<span class="star">
							<el-rate
									v-model="seller.foodScore"
									disabled
									text-color="#ff9900"
									>
							</el-rate>
						</span>
          </p>
          <p class="timeout">
            <span>送达时间</span>
						<span class="delTime">
							{{seller.deliveryTime}}分钟
						</span>
          </p>
        </div>
      </div>
      <div class="bg"></div>
      <div class="content">
        <!-- <div class="cont-top">
          <div class="all" @click='select(2)'>{{types[2]}}</div>
          <div class="good" @click='select(0)'>{{types[0]}}</div>
          <div class="sad" @click='select(1)'>{{types[1]}}</div>
        </div>
        <div class="cont-mid" @click='toggle' :class='{"on" : onlyContent}'>
          <i class="icon iconfont icon-103 gou" :class='{"on" : onlyContent}'></i>
          <p>
            只看有内容的评价
          </p>
        </div> -->
				<v-select></v-select>
      </div>

      <div class="content2" >
        <div class="box" v-bind:key="k" v-for="(v,k) in selectRatings">
          <div class="row1">
            <img :src="v.avatar" class="header">
            <div class="right">
              <div class="user">
                <p>{{v.username}}</p>
                <p class="time">{{v.rateTime | time }}</p>
              </div>
              
              <div class="rank">
									<el-rate
											v-model="v.score"
											disabled
											text-color="#ff9900"
											>
									</el-rate>
                <span>80分钟送达</span>
              </div>
            </div>
          </div>
          <div class="row2">
            <p class="evaluation">{{v.text}}</p>
          </div>
          <div class="row3">
            <div class="zan">
              <i class="icon iconfont icon-zan zan"></i>
            </div>

            <div class="foods" :key="k" v-for="(n,k) in v.recommend">
              <div class="food">
                {{n}}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
	</div>
</template>

<script>
	
  import { mapGetters,mapState} from 'vuex';
	import BScroll from 'better-scroll';
	import time from '../../time';
	import select from '../select/select.vue';

	export default{
		computed : {
			...mapGetters([
        'seller',
				'ratings',
				'types',
				'selectRatings',
				'onlyContent'
			])
		},
		components:{
			'v-select':select,
		},
		methods:{
			// select( type ){
			// 	this.$store.dispatch('select',{type});
			// },
		// 	toggle(){
		// 		this.$store.dispatch('toggle');
		// 	}
		},
    watch:{
			// seller(){
			// 	this.$nextTick( () => {
			// 		if( !this.sellerSCroll ){
			// 			this.sellerSCroll = new BScroll( document.getElementById('main') ,{
			// 			});
			// 		}else{
			// 			this.sellerSCroll.refresh();
			// 		}
			// 	})
			// }
			// selectRatings(){
			// 	if( !this.ratingBScroll ){
			// 		this.ratingBScroll = new BScroll( this.$refs.ratingWrap ,{
			// 			click : true
			// 		})
			// 	}else{
			// 		this.ratingBScroll.refresh();
			// 	}
			// }
		},
		mounted(){
			setTimeout( () => {
				this.$nextTick( () => {
					if( !this.sellerSCroll ){
						this.sellerSCroll = new BScroll( document.getElementById('main') ,{
							click:true
						});
					}else{
						this.sellerSCroll.refresh();
					}
				})
				
			});

			// this.$store.dispatch('changTypes',['推荐','吐槽','全部']);
		}
	}
</script>

<style lang='less' scoped>
	.ratings{
    width:100%;
    height: 10rem;
		color:rgb(7,17,27);
    font-size: 0.24rem;
    z-index: -1000;
    overflow: hidden;
		.top{
			border-bottom:0.01rem solid #e6e5e2;
			padding:0.38rem 0;
			display: flex;
			.left{
				height:1.32rem;
				text-align: center;
				border-right:0.01rem solid #e6e5e2;
				width:2.75rem;
				.score{
					font-size:0.48rem;
					color:rgb(255,153,0);
				}
				.rat1{
					margin:0.16rem 0 0.16rem 0;
				}
				.rat2{
					color:#999;
					font-weight: 200;
				}
			}
			.right{
				box-sizing: border-box;
				height:1.32rem;
				border-left:0.01rem solid #e6e5e2;
				width:4.74rem;
				padding:0 0.48rem;
				line-height:0.36rem;
				.one{
					padding:0.12rem 0;
				}
				
			}
			
		}
		.bg{
			height: 0.36rem;
			width:100%;
			background: #f1f0ee;
			border-bottom:1px solid #e5e6e2;
		}
		.content{
			padding:0 0.36rem;
			.cont-mid,.cont-top{
				box-sizing:border-box;
				display:flex;
				&.on{
					color: #00a0dc;
				}
			}
		}
		.content2{
			border-top:1px solid #e5e6e2;
			.box{
				border-bottom:1px solid #e5e6e2;
				padding:0.36rem;
				.row1{
					display:flex;
					.header{
						width:0.56rem;
						height:0.56rem;
						border-radius:0.5rem;
						margin-right:0.24rem;
					}
					.right{
						p,span{
							font-size:0.2rem;
						}
						.user{
							display:flex;
							.time{
								color:gray;
								margin-left:3rem;
							}
						}
						.rank{
							margin-top:0.08rem;
							// height:0.2rem;
							span{
								margin-left:0.12rem;
								color:rgb(147,153,158);
								font-weight:200;
							}
						}
					}
				}
				.row2{
					margin:0.12rem 0 0.16rem 0.8rem;
					.evaluation{
						max-height:0.7rem;
						line-height:0.36rem;
					}
				}
				.row3{
					margin-left:0.8rem;
					display:flex;
					div{
						margin-right:0.16rem;
					}
					.zan{
						font-size:0.24rem;
						color:rgb(0,160,220)
					}
					.foods{
            // display:flex;
            overflow: hidden;
						.food{
							border:1px solid #e5e6e2;
							border-radius:0.02rem;
							padding:0 0.12rem;
							font-size:0.18rem;
							line-height:0.32rem;
							// width:1.2rem;
							white-space: nowrap;
							overflow: hidden;
              text-overflow: ellipsis;
              float: left;
            }
            // .food:nth-chlid(n+5){

            // }
					}
					
				}
				
			}

		}
	}

.star{
	float: right;
}
.delTime{
margin-left: 0.4rem;

}
</style>


