<template>
    <div class="content">
        <div class="public">
            <p class="title">公告与活动</p>
            <div class="con">
                <p >{{seller.bulletin}}</p>
            </div>
        </div>
        <!-- 优惠 -->
        <div class="supports">
            <div class="list" v-for="(v,k) in seller.supports" :key="k">
                <div class="imgs">
                    <img src="../../../static/images/discount.png" alt="123">
                </div>
                <p>{{v.description}}</p>
            </div>
            
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    export default {
        computed:{
            ...mapGetters([
            'seller'
            ])
        }
    }
</script>

<style lang="less" scoped>
.content{
    width: 100%;
}

.public{
    width: 100%;
    padding: 0.36rem;
    border-bottom: rgba(7,17,27,0.1) solid 0.01rem;
    box-sizing: border-box;
    .title{
        height: 0.28rem;
        font-size: 0.28rem;
        color: rgb(7,17,27);
        line-height: 0.28rem;
    }
    .con{
        width: 100%;
        font-size: 0.24rem;
        font-weight: 200;
        color: rgb(240,20,20);
        line-height: 0.48rem;
        overflow: hidden;
        p{
            margin:0.16rem 0.24rem 0.24rem 0.32rem;
        }
    }
}

// 优惠
.supports{
    width: 100%;
    line-height: 0.96rem;
    box-sizing: border-box;
    padding: 0 0.24rem 0 0.24rem;
    .list{
        width: 100%;
        height: 0.96rem;
        overflow: hidden;
        border-bottom: rgba(7,17,27,0.1) solid 0.01rem;
        box-sizing: border-box;
        .imgs{
            width: 0.32rem;
            height: 100%;
            float: left;
            margin: 0 0.12rem 0 0.24rem;
            line-height: 1.14rem;
            img{
                width: 0.32rem;
                height: 0.32rem;
            }
        }
        
        p{
            float: left;
            font-size: 0.24rem;
            font-weight: 200;
            color: rgb(7,17,27);
        }
    }
}

</style>



